@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --white-shady: rgb(255, 255, 255, .95);
  --white: rgb(255, 255, 255);
  --black-pitch: rgb(17, 17, 17);
  --black-light: rgb(17, 17, 17, .8);
  --black-transparent: rgb(17, 17, 17, .2);
  --black-less-transparent: rgb(17, 17, 17, .7);
  --shadow: rgb(0, 0, 0, .1);
  --green: rgb(0, 204, 102);
  --blue: rgb(0, 123, 255);
  --red: rgb(245, 66, 66)
}


/* Applies to the html document */
* {
  margin:0;
}
/* Styling app component */
.app {
  background-color: rgb(17, 17, 17);
  background-color: var(--black-pitch);
}
/* Positioning banner and color */
.banner {
    color: var(--white);
    object-fit: contain;
    height: 448px;
}

/* Positioning banner content */
.banner__contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

/* Styling banner title */
.banner__title {
    font-size: 1.5rem;
    font-weight: 800;
    text-shadow: 2px 2px 4px var(--shadow);


}

/* Styling banner description font*/
.banner__description {
    width: 45rem;
    font-size: 1.2rem;
    max-width: 80vw;
    max-height: 300px;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);

}

/* Styling buttons */
.banner__button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-bottom: 1rem;
    background-color: rgba(51, 51, 51, 0.5);
}

/* Styling hover effect */
.banner__button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}

/* Creates a fade in banner bottom effect */
.banner--fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), var(--black-pitch))
}

.banner__button11 {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
}

.banner__button111 {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
}
.alternate__container{
    color: var(--white-shady);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: end;
    
}

.rows{
    padding-top:30px;
    padding-bottom: 10px;
    background-color: var(--black-pitch);
    /* min-height = viewport height - banner height - rows vertical padding */
    min-height: calc(100vh - 448px - 30px);
}

/* Positioning rows to right */
/* Setting min-height is necessary to lazy load the row otherwise, it would be 0 and all of the remaining rows would be loaded at once */

.row {
    /* margin-left: 20px; */
    /* min-height: 250px; */
}

.row__title{
    margin-left: 20px;
    color: var(--white);
    margin-bottom: 10px;

}

/* Positioning in flex */
.row__posters {
    display: flex;
    /* Makes title stay while scrolling */
    overflow-y: hidden;
    overflow-x: scroll;
    margin-left: 20px;
    padding-bottom: 20px;
    padding-top: 10px;
}

/* Removes scrollbar, but keeps functionality */
.row__posters::-webkit-scrollbar {
    display: none;
}



  

.row__poster {
    width: 200px; /* Adjust the width as per your needs */
    max-height: 300px; /* Adjust the height as per your needs */
    margin-right: 10px;
    position: relative; /* Add position relative */
    transition-duration: 450ms;
}

.row__poster:hover{
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
    transition-duration: 450ms;
    z-index: 1;
}

.poster__image{
    width: 200px; /* Adjust the width as per your needs */
    /* height: 300px; */
    height: 200px;
    max-height: 300px; /* Adjust the height as per your needs */
    margin-right: 10px;
    /* border-radius: 10px; */
}

.row__poster:hover .poster__details{
    display: block;
}

.poster__details {
    max-height: 250px;
    width: 200px;
    padding: 6px;
    position: absolute;
    box-sizing: border-box;
    color: white;
    bottom: 0px;
    background: rgb(27, 27, 27, .8);
    display: none;

}


.poster__title {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 5px; /* Add some spacing between elements */
}


.poster__labels {
    font-size: 10px;
}


/* @import 'App.css'; */
:root {
  --menu-size: 19px;
  --menu-size-hover: 20px;
}

/* Styling and positioning navbar container */
.nav {
  position: fixed;
  top: 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-self: center;
  padding: 20px;
  height: 30px;
  z-index: 1;
  /* Navbar always stays on top of page */
  /* Animations */
  transition-timing-function: ease-in;
  transition: all 0.5s;
  background-color: var(--black-transparent);
}

/* Adds a black color on navbar */
.nav__black {
  background-color: var(--black-pitch);
}

/* Styling and positioning navbar logo */
.nav__logo {
  /* position: fixed; */
  float: left;
  left: 20px;
  margin-top: -5px;
  width: 120px;
  object-fit: contain;

}



@media (max-width: 750px) {
  .nav__avatar {
    display: none;
  }
}


.nav__search {
  color: var(--white-shady);
  position: fixed;
  right: 100px;
  margin-top: 0px;
  background-color: transparent;

}

.search__box {
  background-color: transparent;
  border: none;
  float: right;
}

/* Styling and positioning navbar avatar */
.nav__avatar {
  position: fixed;
  right: 20px;
  width: 40px;
  object-fit: contain;
}


/* Home button */

.nav__menu {
  display: flex;
  position: fixed;
  left: 160px;
  color: var(--white-shady);
  grid-gap: 10px;
  gap: 10px;
  font-size: 19px;
  font-size: var(--menu-size);
  font-weight: 500;
  transition: 2s ease-in-out;
}


.icon-bar {
  width: 20px;
  height: 2px;
  background-color: var(--white-shady);
  margin: 4px 0;
}


.home__button {
  cursor: pointer;

}

.collections__button {
  cursor: pointer;
}

.home__button:hover,
.collections__button:hover {
  color: var(--white);
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition-duration: 250ms;
}

.navbar-toggle {
  position: fixed;
  left: 180px;
  height: 40px;
  width: 40px;
  cursor: pointer;
}

/* To toggle the icon bar and menu items*/

@media (max-width: 539px){
  .nav__menu{
    display: none;
    padding: 10px;
  }
  .nav{
    background-color: var(--black-less-transparent);
  }
  
  .nav__menu.open{
    display:flex;
    flex-direction: column;
    align-items: flex-start;
    display: block;
    top: 70px;
    transition: 2s;
    background-color: var(--black-less-transparent);
    z-index: 2;
  }
  .navbar-toggle{
    display: block;
  }
}

@media (min-width: 540px){
  .navbar-toggle{
    display: none;
  }
  .nav__menu{
    display: flex;
    flex-direction: row;
  justify-content: space-between;

  }
  
}

.search {
  width: 100%;
  position: relative;
  display: flex;
  background: transparent;
}

.search__term {
  width: 100%;
  border: 1.5px solid var(--white-shady);
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: var(--white-shady);
  background: transparent;

  box-shadow: 0 2px 4px var(--shadow);


}


.search__button {
  width: 40px;
  height: 32.5px;
  border: 1.5px solid var(--white-shady);
  background: transparent;
  text-align: center;
  color: var(--white-shady);
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.search:hover .search__term,
.search:hover .search__button {
  border-color: var(--white);
}

/*Resize the wrap to see the search bar change!*/
.search__wrap {
  width: 30%;
  position: fixed;
  right: 100px;
  top: 20px;
  background: transparent;
}

